<template>
  <div class="add">
    <div class="form-wrapper">
      <el-form :model="ruleForm" :size="size" :rules="rules" ref="ruleForm" class="rule-form" label-width="100px">
        <label class="title">样板基本信息</label>
        <el-form-item label="产品编号" prop="templateId">
          <el-input v-model="ruleForm.templateId"></el-input>
        </el-form-item>
        <el-form-item label="所属系列" prop="templateSeries">
          <el-input v-model="ruleForm.templateSeries"></el-input>
        </el-form-item>
        <el-form-item label="规格" prop="templateNorm">
          <el-input v-model="ruleForm.templateNorm"></el-input>
        </el-form-item>
        <el-form-item label="每套片数" prop="piecesPerSuit">
          <el-input v-model="ruleForm.piecesPerSuit"></el-input>
        </el-form-item>
        <el-form-item label="类型" prop="templateType">
          <el-select v-model="ruleForm.templateType" style="width: 100%;">
            <el-option label="标准版" value="standard"></el-option>
            <el-option label="确认版" value="confirmed"></el-option>
          </el-select>
        </el-form-item>
        <hr>
        <label class="title">样板附属信息</label>
        <el-form-item label="签板人" prop="templateSignaturer">
          <el-input v-model="ruleForm.templateSignaturer"></el-input>
        </el-form-item>
        <el-form-item label="色号" prop="templateColor">
          <el-input v-model="ruleForm.templateColor"></el-input>
        </el-form-item>
        <el-form-item label="生产日期" prop="templateCtime">
          <el-date-picker
            v-model="ruleForm.templateCtime"
            type="datetime"
            style="width: 100%;"
            placeholder="选择日期时间">
          </el-date-picker>
        </el-form-item>
      </el-form>
      <el-button type="primary" @click="submitForm('ruleForm')">提交</el-button>
      <el-button @click="resetForm('ruleForm')">重置</el-button>
    </div>
  </div>
</template>

<script>
  import * as api from 'api/index'
  export default {
    name: "add",
    data() {
      var numberValidate = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('This field is required'))
        }
        else if (isNaN(value)) {
          callback(new Error('Please enter a number'))
        }
        else {
          callback()
        }
      }
      
      return {
        size: 'medium',
        ruleForm: {
          templateId: '',
          templateSeries: '',
          templateNorm: '',
          piecesPerSuit: '',
          templateType: '',
          templateSignaturer: '',
          templateColor: '',
          templateCtime: ''
        },
        rules: {
          templateId: [
            { required: true, trigger: 'blur' }
          ],
          templateSeries: [
            { required: true, trigger: 'blur' }
          ],
          templateNorm: [
            { required: true, trigger: 'blur' }
          ],
          piecesPerSuit: [
            { validator: numberValidate, trigger: 'blur' }
          ],
          templateType: [
            { required: true, trigger: 'change' }
          ],
          templateSignaturer: [
            { required: true, trigger: 'blur' }
          ],
          templateColor: [
            { required: true, trigger: 'blur' }
          ],
          templateCtime: [
            { type: 'date', required: true, trigger: 'change' }
          ]
        }
      }
    },
    methods: {
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            var t = JSON.parse(JSON.stringify(this.ruleForm))
            api.Add.r(t).then((res) => {
              if (res.data === '恭喜您，操作成功!') {
                this.$refs[formName].resetFields()
                this.$router.push('/info')
              }
              else {
                this.$message.error('未知错误')
              }
            })
          } else {
            console.log('error submit!!')
            this.$message.error('服务器错误')
            return false
          }
        })
      },
      resetForm(formName) {
        this.$refs[formName].resetFields()
      }
    },
    created() {
      this.size = (window.innerWidth < 767) ? 'mini' : 'medium'
    }
  }
</script>

<style type="text/stylus" lang="stylus" rel="stylesheet/stylus" scoped>
  @import "~assets/stylus/variable.styl"
  @import "Add.styl"
</style>



